import { Events } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ViewController } from 'ionic-angular';
import { BlogProvider } from './../../../providers/blog-provider';
/**
 * Generated class for the TopicChooserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-topic-chooser',
  templateUrl: 'topic-chooser.html',
})
export class TopicChooserPage {

  allTopic:any;

  constructor(public navCtrl: NavController,
     public navParams: NavParams,
     public blogProvider:BlogProvider,
     public viewCtrl:ViewController,
     public events:Events
     ) {
    this.allTopic=this.initTopics();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TopicChooserPage');
  }

  initTopics(){
    return this.blogProvider.initTopics().sort((a, b) => {  
      return b.discussCount - a.discussCount;
    });
  }

  topicSelected(topic){
    this.viewCtrl.dismiss();
    this.events.publish('topic:choosen',topic);
  }

}



